  <template>
    <v-app-bar color="primary">
      <v-app-bar-nav-icon></v-app-bar-nav-icon>
      <v-app-bar-title>
        SamplePage
      </v-app-bar-title>
      <!--v-btn>SignIn</v-btn-->
      <v-btn icon="mdi-home" class="ma-2" to="/"></v-btn>
    </v-app-bar>
    <v-container>
      <!--v-system-bar color="secondary">
        System Bar
      </v-system-bar-->
      <h1>{{ msg }}</h1>
      <p></p>
      <v-btn @click="countUp">Test</v-btn>
      <hr/>
      <p></p>
      <v-btn @click="countUp" color="blue">Test</v-btn>
      <hr/>
      <p></p>
      <v-btn @click="countDown" color="primary">Test</v-btn>
      <hr/>
      <p></p>
      <v-btn @click="countDown" outlined color="blue">Test</v-btn>
      <hr/>
      <p></p>
      <v-btn class="ma-2" to="/">Home</v-btn>
      <hr/>
      <p></p>
      <v-sheet width="80%">
        <v-text-field label="" variant="outlined" placeholder="" counter="20" clearable></v-text-field>
      </v-sheet>
      <hr/>
      <p></p>
      <v-select label="" :items="['','','','']" class="ma-2" v-model="select"></v-select>
      <hr/>
      <p></p>
      <v-slider v-model="slideval" color="black" label="" :min="0" :max="100" :step="1" thumb-label></v-slider>
      <h1>{{ slideval }}</h1>
      <hr/>
      <p></p>
      <div class="d-flex justify-center" >
        <div>
          <v-radio-group v-model="radioGroup">
            <div class="d-flex flex-row" >
              <v-radio label="Hoge1" value="1"></v-radio>
              <v-radio label="Hoge2" value="2"></v-radio>
              <v-radio label="Hoge3" value="3"></v-radio>
            </div>
          </v-radio-group>  
        </div> 
      </div>
      <h1>{{ radioGroup }}</h1>
      <hr/>
      <p></p>
      <v-row>
      <v-col>
        <v-checkbox v-model="selected" value="1" label="Hoge1">
        </v-checkbox>
      </v-col>
      <v-col>
        <v-checkbox v-model="selected" value="2" label="Hoge2" color="red">
        </v-checkbox>
      </v-col>
      <v-col>
        <v-checkbox v-model="selected" value="3" label="Hoge3" color="blue">
        </v-checkbox>
      </v-col>
      </v-row>
      <h1>{{selected}}</h1>
      <hr/>
        <p></p>
        <v-btn color="primary">
          Open Dialog
          <v-dialog v-model="dialog" activator="parent" >
            <v-sheet>
              <v-sheet class="my-2 mx-5">
                <h2>Dialog!</h2>
                <p class="my-4">
                  
                </p>
                <v-btn color="primary" block @click="dialog = false">Close</v-btn>
              </v-sheet>
            </v-sheet>
          </v-dialog>
        </v-btn>
      <hr/>
      <p></p>
      <v-list @click:select="clickItem">
      <v-list-item value="1">
        <v-list-item-title>HOGE1</v-list-item-title>
        </v-list-item>
      <v-list-item value="2">
        <v-list-item-title>HOGE2</v-list-item-title>
        </v-list-item>
      <v-list-item value="3">
        <v-list-item-title>HOGE3</v-list-item-title>
        </v-list-item>
      </v-list>
      <h1>{{ message }}</h1>
      <hr/>
      <p></p>
      <v-data-table
        v-model:items-per-page="itemsPerPage"
        :headers="headers"
        :items="dataset"
        item-value="name"
        class="elevation-1"
      ></v-data-table>
    </v-container>
  </template>
  
  <script>
  import { VDataTable } from 'vuetify/lib/labs/components.mjs';

  export default {
    name: "CountUpDown",

    props: {
      msg: String
    },

    data () {
      return {
        count: 0,
        message: "",
        select: null,
        selected: [],
        slideval: 0,
        radioGroup: "3",
        dialog: false,
        itemsPerPage: 5,

        headers: [
          {
            title: '',
            align: 'start',
            sortable: false,
            key: 'name',
          },
          { title: 'no', align: 'end', key: 'no' },
          { title: '', align: 'end', key: 'area' },
        ],

        dataset: [
          {name: '', no: 1,  area: 83456.20 },
          {name: '', no: 2,  area: 13562.23 },
          {name: '', no: 3,  area: 2187.42 },
          {name: '', no: 4,area: 4726.12 },
          {name: '', no: 5,  area: 1876.47 },
          {name: '', no: 6,area: 2415.84 },
          {name: '', no: 7,  area: 7113.00 },
          {name: '', no: 8,area: 9187.80 },
          {name: '', no: 9,  area: 7285.73 },
          {name: '', no: 10, area: 4185.47 },                                
        ],
      }
    },

    components: {
      VDataTable,
    },

    methods: {
      countUp(){
        this.count++;
      },
      countDown(){
        this.count--;
      },
      touchStart(e){
        e.target.classList.add('touched');
      },
      touchEnd(e){
        e.target.classList.remove('touched');
      },
      clickItem(arg){
      this.message = arg.id;
      }
    },
  
  };
  </script>
  
  <style>
  hr {
		height:0px;
		margin:0 0 10px 0;
		border-color:#cccccc;
		border-style:solid;
		border-width:0 0 1px 0;
		padding:0 0 10px 0;
	}
  </style>